import React, {Component} from 'react';
import CoinDetails from './CoinDetails.jsx';
import '../styles.css';
import {SimpleSelect, MultiSelect} from 'react-selectize';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      query: '',
      data: '',
      coinslist: [],
      isLoading: false,
      selected: ''
    }
  }

  getdata(value) {
    this.setState({isLoading: true})
    const BASE_URL = 'https://api.coinmarketcap.com/v1/ticker/'
    const FETCH_URL = `${BASE_URL}/${value === ""
      ? ""
      : value.label}`;
    fetch(FETCH_URL, {method: 'GET'}).then(response => response.json()).then(json => {
      let results = json;
      const coinnametest = results.map(e => {
        return {
          label: e.id + "-" + e.symbol,
          value: e.id
        }
      })
      this.setState({data: results, coinslist: coinnametest});
    }).catch(error => this.setState({error: "Opps! Error loading try refreshing the page"}))
    // e.preventDefault(); //avoid auto refresh of page
  }

  componentDidMount() {
    this.getdata("");
  }

  showselected = (value) => {
    if (Array.isArray(value)) {
      const selectedcoins = []
      value.map(element => {
        console.log("element", element)
        const coin = element.value;
        function findcoin(e) {
          return e.id === coin
        }
        selectedcoins.push(this.state.data.find(findcoin));
      })
      this.setState({selectedcoins: selectedcoins})
      console.log(selectedcoins)
    }
  }

  render() {

    return (

      <div>
        <div className="header">
          <svg className="menu svg" xmlns="http://www.w3.org/2000/svg" width="226.777" height="226.777" viewBox="0 0 226.777 226.777"><path d="M112.553 157V86.977l-68.395 29.96zm0-74.837V-.056L46.362 111.156zM116.962-.09v82.253l67.121 29.403zm0 87.067v70.025l68.443-40.045zm-4.409 140.429v-56.321L44.618 131.31zm4.409 0l67.935-96.096-67.935 39.775z"/></svg>
          <span className="menu">MENU</span>
          <span className="menu">HOME</span>
          <div className="menu">ABOUT</div>
          <div className="menu">CONTACT</div>
        </div>
        {this.state.error}
        <div className="section">
          <img src="" alt=""/>
          <svg className="eth" xmlns="http://www.w3.org/2000/svg" width="226.777" height="226.777" viewBox="0 0 226.777 226.777"><path d="M112.553 157V86.977l-68.395 29.96zm0-74.837V-.056L46.362 111.156zM116.962-.09v82.253l67.121 29.403zm0 87.067v70.025l68.443-40.045zm-4.409 140.429v-56.321L44.618 131.31zm4.409 0l67.935-96.096-67.935 39.775z"/></svg>
          <MultiSelect
            options={this.state.coinslist}
            placeholder="Select coins"
            transitionEnter={true}
            transitionLeave={true}
            maxValues={4}
            value="ab"
            onValuesChange= {(value) => {
              this.showselected(value) }}/>
        </div>
        <div className="app">
          <CoinDetails data={this.state.selectedcoins}></CoinDetails>
        </div>
      </div>
    );
  }
}

export default App;



