import React, { Component } from 'react';

class CoinDetails extends Component {
  constructor(props){
    super(props);
    this.state =({
      data: []
    })
  }

  test(){
    console.log("test called", this.props.data);
  }

  componentWillMount(){
    // console.log("componentWillMount was called", this.props)
    // this.setState({data: this.props});
  }

  componentDidMount(){
    // console.log("componentDidMount was called", this.props)
    // this.setState({data: this.props});
  }

  componentDidUpdate(){
    // console.log("componentDidUpdate props.data", this.props.data);
  }

render(){
  const data = Array.isArray(this.props.data)? this.props.data: [];
  // console.log("data logged", Array.isArray(data))
  return(
    // <div>{console.log(Array.isArray(data))}</div>
    <div>{
      data.map((curr,ind) =>
      <div key={ind} className="coindetails w3-animate-right">
      <div className="coinsymbol">{curr.symbol}</div>
      <div className="coinname">{curr.name}</div>
      <div>Price USD: {curr.price_usd}</div>
      </div>)
    }</div>
  )
}

}

export default CoinDetails



